.Settings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 70%;
  margin: auto;
  font-size: 12px;
  -ms-flex-negative: 2;
      flex-shrink: 2;
}

.Settings > * {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Settings .expandedView {
  position: relative;
  top: 4px;
}

.Settings .settings-label.expandedView {
  margin-bottom: -8px;
}

.Settings button#add-summer-button {
  background: white;
  border: 1px solid black;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  height: 50%;
  margin: auto;
}

.Settings button#add-summer-button:active {
  background: lightgray;
}

