.Validators {
  font-size: 12px;
  width: 100%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
}

.Validators > div {
  margin: 0 10px;
  width: 100%;
}

.Validators .progress-group {
  position: relative;
}

.Validators .progress-bar {
  height: 15px;
  width: 100%;
  background: lightgray;
  border-radius: 10px;
  overflow: hidden;
  margin: 3px 0;
}

.Validators .progress-completed {
  background: lightcoral;
  height: 100%;
  border-radius: inherit;
}

.Validators .progress-popup {
  background: hsl(225, 100%, 96%);
  border: 1px solid rgba(170, 177, 197, 0.7);
  border-radius: 5px;
  padding: 4px;
  position: absolute;
  bottom: 30px;
  width: 100%;
  left: 0px;
  bottom: 100%;
  z-index: 99999;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: none;
}
.Validators .progress-popup::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  border: 12px solid transparent;
  border-top: 12px solid rgb(170, 177, 197);
}
.Validators .progress-group.geneds:hover .progress-popup {
  display: block;
}
.Validators .progress-group.major-courses:hover .progress-popup {
  display: block;
}
.Validators .progress-group.credits:hover .progress-popup {
  display: block;
}

.App .saving-schedule-container {
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 2;
      flex-shrink: 2;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: auto;
  margin-bottom: auto;
}



